
.CircularProgressbar .CircularProgressbar-path {
  stroke: #0ef3c5 !important;
  stroke-opacity: 0.8;

}

.CircularProgressbar .CircularProgressbar-trail {
  stroke: #038298 !important;
  stroke-opacity: 0.2;
}

.CircularProgressbar .CircularProgressbar-text {
  fill: #0ef3c5 !important;
  font-size: 20px;
  align-content:center;
  opacity: 0.9;
}

.CircularProgressbar {
  top:-1;
}

.parent {
  width: 80%;
  height: 80%;
  display: flex;
  align-self: center;
  justify-content: center;
  border: 1px dotted black;
  align-items:center;
}
.child {
  position:absolute;
  margin: auto auto;
  display:inline-block;
}
.timer {
  width: 30vw;
  height:30vw;
  min-height: 500px;
  min-width:500px;
  display:flex;
  flex-direction: column;
  justify-items: center;
  border: 1px dotted black;
  align-items: stretch;
  gap: 5%;
}
.timer_button {
  border: 1px dotted black;
  display:flex;
  width:100%;
  height:15%;
  justify-content: center;
  align-items: center;
}


.text, .activeInput, .disabledInput {
  color: #0ef3c5;
  font-size:min(150px,max(4vw, 10px));
  opacity: 1;
}

.activeInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border: none;
  display: inline-block;
}

.disabledInput{
  width:2ch;
  background: transparent;
  position: relative;
  border: 1px solid #FFF;
  border:none;
  display: inline-block;
}
button {
  background-color: transparent;
  color: rgba(255,255,255,0.8);
  border:none;
  width: 4vw;
  min-width: 50px;
  opacity: 0.5;
  transition:0.2s;
}

button:hover{
  opacity: 1;
  transition:0.8;
}
button:active{
  opacity: 1;
  transition:0.8s;
}